<template>
    <div class="index">
        <Row :gutter="16" class="main">
            <Col v-for="(mod, index) in modules"
                 :span="6"
                 :key="index">
                <Card class="module" @click.native="toModule(mod)">
                    <div style="text-align:center">
                        <Icon class="icon" :type="mod.icon" :color="mod.color"></Icon>
                        <h3 class="name">{{mod.name}}</h3>
                        <span class="desc">{{mod.desc}}</span>
                    </div>
                </Card>
            </Col>
        </Row>

        <div class="sidebar">
            <Avatar v-if="user" :style="{background: '#f56a00'}">{{ user.name }}</Avatar>
            <settings/>
        </div>

        <div class="floor">
            <span class="notice" @click="showNotice">使用注意事项</span>
        </div>
    </div>
</template>
<script>
    import Settings from "@cmp/settings";
    import {getStore, setStore} from "@/assets/js/utils/store-utils";

    export default {
        components: {Settings},
        data() {
            return {
                user: null,
                modules: [{
                    name: "聚合搜索引擎",
                    icon: "md-search",
                    color: "#2db7f5",
                    desc: "集成多个搜索引擎聚合搜索相关关键字并支持将搜索结果添加至结果汇总列表",
                    path: "search/index"
                }, {
                    name: "搜索结果队列",
                    icon: "md-list",
                    color: "#19be6b",
                    desc: "将聚合搜索引擎结果汇总并查看列表",
                    path: "search/result"
                }]
            }
        },
        methods: {
            showNotice() {
                electronAPI.invoke({type: "getReadme"}).then(readme => {
                    this.$Modal.info({
                        title: "使用注意事项",
                        width: 700,
                        content: readme
                    });
                });
            },
            toModule(mod) {
                this.$router.push(mod.path);
            }
        },
        created() {
            electronAPI.invoke({type: "getUser"}).then(user => {
                this.user = user;
            })
        },
        mounted() {
            if (!getStore({name: "show_notice"})) {
                this.showNotice();
                setStore({name: "show_notice", content: true})
            }
        }
    }
</script>
