                .icon {
                    font-size: 35px;
                }

                .name {

                }

                .desc {
                    color: @subsidiary-color;
                }
            }
        }

        .sidebar {
            width: 50px;
            text-align: center;
            display: flex;
            flex-direction: column;
            flex-wrap: nowrap;
            align-items: center;

            & > * {
                margin-bottom: @margin-size;
            }
        }

        .notice {
            position: absolute;
            z-index: 9;
            width: 100%;
            left: 0;
            bottom: 0;
            padding: @padding-size;
            text-align: center;
            color: @primary-color;
            cursor: pointer;
        }
    }
</style>
