<template>
    <div class="settings">
        <Icon class="setting" type="md-cog" @click="open"/>
        <Modal
            v-model="show"
            fullscreen
            title="设置"
            width="800"
            ok-text="保存设置"
            @on-ok="save"
            @on-cancel="cancel">
            <div class="settings-main">
                <Form :model="config" v-if="config" label-position="left" :label-width="120">
                    <Alert type="info" show-icon>当前设备ID：{{machineId}}，默认提供本地认证可自行修改认证地址</Alert>
                    <Alert type="warning" show-icon>页面地址修改后重启应用程序生效，如输入无效地址将导致程序无法使用</Alert>
                    <FormItem label="页面地址">
                        <Input v-model="config.host"></Input>
                    </FormItem>
                    <FormItem label="认证地址">
                        <Input v-model="config.allowCodes"></Input>
                    </FormItem>
                    <Row :gutter="16">
                        <Col span="12">
                            <FormItem label="查询间隔（ms）">
                                <Input v-model="config.search.interval" type="number"></Input>
                            </FormItem>
                        </Col>
                        <Col span="12">
                            <FormItem label="超时时间（ms）">
                                <Input v-model="config.search.timeout" type="number"></Input>
                            </FormItem>
                        </Col>
                    </Row>
                    <FormItem label="搜索规则">
                        <Row class="rules-input" v-for="(rule, index) in config.search.rules" :key="index">
                            <Col span="2">
                                <FormItem>
                                    <Input v-model="rule.type" placeholder="Type"></Input>
                                </FormItem>
                            </Col>
                            <Col span="4">
                                <FormItem>
                                    <Input v-model="rule.name" placeholder="Name"></Input>
                                </FormItem>
                            </Col>
                            <Col span="13">
                                <FormItem>
                                    <Input v-model="rule.regexp" placeholder="RegExp"></Input>
                                </FormItem>
                            </Col>
                            <Col span="2">
                                <FormItem>
                                    <Input v-model="rule.flag" placeholder="Flag"></Input>
                                </FormItem>
                            </Col>
                            <Col span="3">
                                <FormItem>
                                    <Button type="error" icon="md-close" @click="removeRule(index)"></Button>
                                    <Button type="primary" v-if="index === config.search.rules.length - 1" icon="md-add"
                                            @click="addRule(index)"></Button>
                                </FormItem>
                            </Col>
                        </Row>
                    </FormItem>
                    <FormItem label="禁止深度搜索主机">
                        <Tag v-for="(item, index) in config.search.noDeepDomains" :key="index" :name="item" closable
                             @on-close="removeDomains(index)">{{item}}
                        </Tag>
                        <Button icon="ios-add" type="dashed" size="small" @click="inputDomains">添加主机</Button>
                    </FormItem>
                    <FormItem label="忽略超链接">
                        <Tag v-for="(item, index) in config.search.ignoreHrefs" :key="index" :name="item" closable
                             @on-close="removeIgnoreHref(index)">{{item}}
                        </Tag>
                        <Button icon="ios-add" type="dashed" size="small" @click="inputHref">添加超链接</Button>
                    </FormItem>
                    <FormItem label="下载文件类型">
                        <Tag v-for="(item, index) in config.search.downloadContentTypes" :key="index" :name="item"
                             closable
                             @on-close="removeContentType(index)">{{item}}
                        </Tag>
                        <Button icon="ios-add" type="dashed" size="small" @click="inputContentType">添加类型</Button>
                    </FormItem>
                    <FormItem label="下载文件后缀">
                        <Tag v-for="(item, index) in config.search.downloadSuffixes" :key="index" :name="item" closable
                             @on-close="removeSuffixes(index)">{{item}}
                        </Tag>
                        <Button icon="ios-add" type="dashed" size="small" @click="inputSuffixes">添加后缀</Button>
                    </FormItem>
                    <Alert type="warning" show-icon>此密钥为默认生成用于加密数据字段，修改AES密钥后将无法解密修改前的数据，请及时导出数据</Alert>
                    <FormItem label="AES密钥">
                        <Input v-model="config.secretKey" type="textarea" :rows="2"
                               placeholder="请输入AES密钥"></Input>
                    </FormItem>
                    <Alert type="warning" show-icon>向量为默认生成用于加密数据字段，修改AES向量后将无法解密修改前的数据，请及时导出数据</Alert>
                    <FormItem label="AES初始化向量">
                        <Input v-model="config.iv" type="textarea" :rows="2"
                               placeholder="请输入AES初始化向量"></Input>
                    </FormItem>
                </Form>
            </div>
        </Modal>
    </div>
</template>

<script>
    import StringUtils from "@js/utils/string-utils";

    export default {
        name: "settings",
        data() {
            return {
                machineId: null,
                show: false,
                config: null,
                form: {
                    contentType: null,
                    suffix: null,
                    href: null,
                    domain: null
                }
            }
        },
        methods: {
            inputDomains() {
                this.$Modal.confirm({
                    render: (h) => {
                        return h('Input', {
                            props: {
                                value: this.value,
                                autofocus: true,
                                placeholder: '请输入主机域名或后缀'
                            },
                            on: {
                                input: (val) => {
                                    this.form.domain = val;
                                }
                            }
                        })
                    },
                    onOk: this.addDomains
                })
            },
            addDomains() {
                let domain = this.form.domain;
                this.config.search.noDeepDomains.push(domain);
                this.form.domain = null;
            },
            removeDomains(index) {
                this.config.search.noDeepDomains.splice(index, 1);
            },
            addRule() {
                this.config.search.rules.push({});
            },
            removeRule(index) {
                this.config.search.rules.splice(index, 1);
                if (this.config.search.rules.length === 0) {
                    this.config.search.rules.push({});
                }
            },
            inputHref() {
                this.$Modal.confirm({
                    render: (h) => {
                        return h('Input', {
                            props: {
                                value: this.value,
                                autofocus: true,
                                placeholder: '请输入超链接'
                            },
                            on: {
                                input: (val) => {
                                    this.form.href = val;
                                }
                            }
                        })
                    },
                    onOk: this.addIgnoreHref
                })
            },
            addIgnoreHref() {
                let href = this.form.href;
                this.config.search.ignoreHrefs.push(href);
                this.form.href = null;
            },
            removeIgnoreHref(index) {
                this.config.search.ignoreHrefs.splice(index, 1);
            },
            inputContentType() {
                this.$Modal.confirm({
                    render: (h) => {
                        return h('Input', {
                            props: {
                                value: this.value,
                                autofocus: true,
                                placeholder: '请输入文件类型'
                            },
                            on: {
                                input: (val) => {
                                    this.form.contentType = val;
                                }
                            }
                        })
                    },
                    onOk: this.addContentType
                })
            },
            addContentType() {
                let type = this.form.contentType;
                if (StringUtils.isBlank(type)) {
                    this.$Message.error("文件类型不能为空")
                    return;
                }
                this.config.search.downloadContentTypes.push(type);
                this.form.contentType = null;
            },
            removeContentType(index) {
                this.config.search.downloadContentTypes.splice(index, 1);
            },
            inputSuffixes() {
                this.$Modal.confirm({
                    render: (h) => {
                        return h('Input', {
                            props: {
                                value: this.value,
                                autofocus: true,
                                placeholder: '请输入文件后缀'
                            },
                            on: {
                                input: (val) => {
                                    this.form.suffix = val;
                                }
                            }
                        })
                    },
                    onOk: this.addSuffixes
                })
            },
            addSuffixes() {
                let suffix = this.form.suffix;
                if (StringUtils.isBlank(suffix)) {
                    this.$Message.error("文件后缀不能为空")
                    return;
                }
                this.config.search.downloadSuffixes.push(suffix);
                this.form.suffix = null;
            },
            removeSuffixes(index) {
                this.config.search.downloadSuffixes.splice(index, 1);
            },
            open() {
                electronAPI.invoke({type: "getMachineId"}).then(res => {
                    this.machineId = res;
                    electronAPI.invoke({type: "getConfig"}).then(res => {
                        this.config = res;
                        this.show = true;
                    });
                });
            },
            save() {
                electronAPI.invoke({type: "saveConfig", content: this.config}).then(() => {
                    this.$Message.success("保存设置成功");
                }).catch((err) => {
                    this.$Message.error("保存设置失败:" + err.message);
                });
            },
            cancel() {

            }
        }
    }
</script>

<style scoped lang="less">
    @import '~view-design/src/styles/index.less';
    @import "~@style/variables.less";

    .settings {
        .ivu-icon {
            font-size: 30px;
            cursor: pointer;
        }
    }
</style>

<style lang="less">
    @import "~@style/variables.less";

    .rules-input {
        .ivu-input {
            text-align: center;
        }
    }

    .settings-main {
        .ivu-form {
            padding: @padding-size;
        }
    }
</style>
